html {
    font-size: 62.5%;
}
body *{
    font-size: 1.6rem ;
    box-sizing: border-box;
    padding: 0;
    margin: 0;
        -webkit-overflow-scrolling: touch;
}
body {
	margin : 0 ;
}
img {
    width: 100%;
    vertical-align: bottom;
}
li {
    list-style: none;
}
/* header  */
.header {
    background-color: #fff;
    display: flex;
    align-items: center;
    padding-right: 30px;
    height: 60px;
    position: fixed;
    top: 0;
    width: 100vw;
    z-index: 2;
    filter: drop-shadow(0px 0px 5px rgba(174,174,174,0.6));
}
/* .header-logo {
    width: 130px;
} */
/* header  */


/* menu_bar */
.menu {
    height: 100vh;
    background-color: #0F172A;
    width: 200px;
    position: fixed;
    left: 0;
    top: 60px;
    transition: ease-in-out .15s;
    z-index: 2;
}
.menu-icon {
    width: 30px;
}

.menu-item {
    text-align: center;
    width: 80%;
    margin: auto;
    border-radius: 5px;
    padding: 5px 0px;
    margin-top: 30px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}
.menu-text {
    font-size: 1.3rem;
    color: #fff;
    font-weight: bold;
    margin-left: 5px;
}
.active {
    background-color: #746AE8;
}
.active path {
    fill: #fff;
}
/* menu_bar */


/* content */
.breadcrumb {
    padding-left: 30px;
    font-size: 1.2rem;
}
.content {
    padding-top: 60px;
    padding-left: 200px;
    height: 100vh;
    transition: ease-in-out .3s;
}
.content-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100px;
    padding: 0 20px;
}
.page-title {
    font-size: 2.2rem;
}

/* main_content */
.main-content {
    width: 100%;
    background-color: #F1F5F9;
    padding-top: 10px;
    padding-bottom: 40px;
}
.head-text {
    font-size: 2rem;
    font-weight: bold;
}
.contact-btn {
    background: #ED314E;
    border: none;
    padding: 5px 30px;
    font-size: 1.5rem;
    border-radius: 30px;
    color: #fff;
    cursor: pointer;

}
.logout-btn {
    background: #BFBFBF;
    border: none;
    padding: 5px 30px;
    font-size: 1.5rem;
    border-radius: 30px;
    color: #fff;
    cursor: pointer;
}

.horizontal-title {
    color: #08b77b;
    border-bottom: solid 2px #08b77b;
    padding-left: 10px;
}

.vertical-title {
    font-size: 1.8rem;
}
.vertical-title::before {
    content: '';
    display: inline-block;
    width: 10px;
    height: 25px;
    background-color: #08b77b;
    vertical-align: bottom;
    border-radius: 3px;
    margin-right: 5px;    
}
.search-btn {
    padding: 0px 50px;
    height: 36px;
    border: none;
    border-radius: 5px;
    background-color: #009A69;
    color: #fff;
    font-weight: bold;
    cursor: pointer;
}
.output-btn {
    padding: 10px 65px;
    border: none;
    border-radius: 5px;
    color: #fff;
    background-color: #009A69;
    font-weight: bold;
    cursor: pointer;
}
.confirm-btn {
    padding: 10px 65px;
    border: none;
    border-radius: 5px;
    color: #fff;
    background-color: #009A69;
    font-weight: bold;
    cursor: pointer;
}
button {
    cursor: pointer;
}
button:hover {
    opacity: 0.8;
}
.modal-cancel-btn {
    border: none;
    padding: 8px 25px;
    border-radius: 5px;
    font-size: 1.4rem;
    font-weight: bold;
    background-color: #cfcfcf;
    color: #545454;
}
.modal-confirm-btn {
    border: none;
    padding: 8px 55px;
    border-radius: 5px;
    background-color: #009A69;
    color: #fff;
    font-weight: bold;
}
.any-btn {
    margin-top: 20px;
    padding: 4px 5px;
    border: none;
    border-radius: 5px;
    font-size: 1.5rem;
    background-color: #009A69;
    color: #fff;
}
.any-icon {
    display: inline-block;
    width: 20px;
    vertical-align: middle;
}

a {
    text-decoration: none;
}
/* モーダル */



.active .menu-icon svg path,
.active .menu-text {
    fill: #fff;
    color: #fff;
}

@media screen and (max-width:900px){
    .menu {
        left: -200px;
    }
    .content {
        padding-left: 0;
    }
    .menu-active {
        left: 0;
        transition: ease-in-out .15s;
    }
}
